import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { get } from 'axios';
import { get as getValue } from 'lodash-es';
import BasicInput from '../BasicInput';
import { transformObjectToArray } from '../../utils/commonUtils';
import {
  INPUT_TYPE_MAP,
  TIP_LEVEL_MAP,
} from '../../config/propsPermittedValueMap';
import {
  COMMON_ERROR_TIPS,
  INPUT_ACCOUNT,
  INFO_INCOMPELETE,
  ACCOUNT_NOT_EXISTED,
} from '../../config/copywriting';
import { REGISTRATION_CHECK } from '../../config/interfaceUrl';

import styles from './index.module.scss';

const LEVEL_LIST = transformObjectToArray(TIP_LEVEL_MAP);
const INPUT_TYPE_LIST = transformObjectToArray(INPUT_TYPE_MAP);
const { DEFAULT, ERROR } = TIP_LEVEL_MAP;
const { ACCOUNT } = INPUT_TYPE_MAP;

class LoginAccountInput extends Component {
  static propTypes = {
    account: PropTypes.string,
    resetInput: PropTypes.bool,
    verifyTip: PropTypes.oneOfType([PropTypes.element, PropTypes.string]),
    accountWasDisableTip: PropTypes.oneOfType([
      PropTypes.element,
      PropTypes.string,
    ]),
    tipLevel: PropTypes.oneOf(LEVEL_LIST),
    inputType: PropTypes.oneOf(INPUT_TYPE_LIST),
    onChange: PropTypes.func,
    onBlur: PropTypes.func,
    forgetCheck: PropTypes.bool, // 忘记密码模块使用时返回检查状态
  };

  static defaultProps = {
    account: '',
    resetInput: false,
    accountWasDisableTip: '',
    onChange: () => {},
    onBlur: () => {},
  };

  constructor(props) {
    super(props);
    this.state = {
      account: '',
      tipLevel: DEFAULT,
      verifyTip: '',
    };
    this.onFocus = this.onFocus.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    const { verifyTip, tipLevel, inputType, account, resetInput } = nextProps;
    if (inputType === ACCOUNT) {
      this.setState({
        verifyTip,
        tipLevel,
      });
    }
    if (resetInput) {
      this.setState({
        account,
      });
    }
  }

  onFocus() {
    this.setState({
      tipLevel: DEFAULT,
      verifyTip: '',
    });
  }

  onChange(ev) {
    const {
      target: { value },
    } = ev;
    const { onChange } = this.props;
    this.setState({
      account: value,
    });
    onChange(ev);
  }

  onBlur = () => {
    this.props.onBlur();
  }

  verifyInput() {
    const { account } = this.state;
    const {
      accountWasDisableTip,
      forgetCheck,
    } = this.props;
    let verifyTip = '';
    let tipLevel = '';
    let params;
    let promise;
    if (!account) {
      tipLevel = ERROR;
      verifyTip = INFO_INCOMPELETE;
      promise = Promise.reject(new Error(verifyTip));
    } else {
      params = {
        identity: account,
        checkIdentity: '',
      };
      promise = get(REGISTRATION_CHECK, {
        params,
      })
        .then(() => {
          // 此情况说明帐户不存在
          return Promise.reject();
        })
        .catch(data => {
          const status = getValue(data, 'response.status');
          if (!data) {
            tipLevel = ERROR;
            verifyTip = ACCOUNT_NOT_EXISTED;
          } else {
            switch (status) {
              case 409:
                tipLevel = DEFAULT;
                verifyTip = '';
                break;
              case 412:
                tipLevel = ERROR;
                verifyTip = accountWasDisableTip;
                break;
              default:
                tipLevel = ERROR;
                verifyTip = COMMON_ERROR_TIPS;
                break;
            }
          }
          if (tipLevel === DEFAULT) {
            if (forgetCheck) return getValue(data, 'response.data');;
            return account;
          }
          return Promise.reject(new Error(verifyTip));
        });
    }
    return promise.finally(() => {
      this.setState({
        verifyTip,
        tipLevel,
      });
    });
  }

  render() {
    const { tipLevel, verifyTip, account } = this.state;
    return (
      <div className={styles.loginAccountInput}>
        <BasicInput
          placeHolder={INPUT_ACCOUNT}
          tipLevel={tipLevel}
          value={account}
          onFocus={this.onFocus}
          onChange={this.onChange}
          onBlur={this.onBlur}
        >
          {verifyTip}
        </BasicInput>
      </div>
    );
  }
}

export default LoginAccountInput;
