import React from 'react';
import PropTypes from 'prop-types';
import { ACCOUNT_WAS_DISABLED } from '../../config/copywriting';
import { REACTIVATION_URL } from '../../config/pageUrl';
import copywritingHelper from '../../utils/copywritingHelper';

import styles from './index.module.scss';

const DisableAccountTip = props => {
  const copywritingList = copywritingHelper(ACCOUNT_WAS_DISABLED);
  const { account } = props;
  return (
    <span>
      {copywritingList.map((item, index) => {
        let text = '';
        switch (index) {
          case 1:
            text = account;
            break;
          case 3:
            text = (
              <a
                className={styles.reactivateLink}
                href={REACTIVATION_URL}
                key={index}
              >
                {item}
              </a>
            );
            break;
          default:
            text = item;
        }
        return text;
      })}
    </span>
  );
};

DisableAccountTip.propTypes = {
  account: PropTypes.string.isRequired,
};

export default DisableAccountTip;
