import React, { Component } from 'react';
import jsCookie from 'js-cookie';
import { connect } from 'react-redux';
import md5 from 'js-md5';
import { get } from 'axios';
import { get as getValue } from 'lodash-es';

import config from '../../utils/config';
import utils from '../../utils/utils';
import { SEND_EMAIL, PRE_RESET_PASSWORD } from '../../config/interfaceUrl';
import {
    TIP_LEVEL_MAP,
    INPUT_TYPE_MAP,
    ACCOUNT_CREATE_PURPOSE
  } from '../../config/propsPermittedValueMap';
import { saveBindLoginInfo } from '../../store/actions';

import {
    FORGET_DESC,
    RESET_PASSWORD,
    NEXT_STEP,
    AREADY_SEND,
    INVALID_VERIFICATION_CODE,
    EMAIL_SENT,
} from '../../config/copywriting';

import CanNotGetVercode from '../../components/CanNotGetVercode';
import BasicButton from '../../components/BasicButton';
import LoginAccountInput from '../../components/LoginAccountInput';
import DisableAccountTip from '../../components/DisableAccountTip';
import VercodeInput from '../../components/VercodeInput';
import confirm from '../../components/Confirm/Confirm';

import styles from './index.module.scss';
import copywritingHelper from '../../utils/copywritingHelper';

const { DEFAULT, ERROR } = TIP_LEVEL_MAP;
const { VERCODE, DEFAULT: INPUT_DEFAULT } = INPUT_TYPE_MAP;
const { FORGET_PASSWORD } = ACCOUNT_CREATE_PURPOSE;

// 重置步骤
const RESET_STEP = {
    INPUT_ACCOUNT: 0,
    INPUT_CODE: 1,
    INPUT_PASSWORD: 2,
}

const ERROR_CODE_VALUE_INVALID="reset.password.pre.signature.error";
const ERROR_CODE_VALUE_NOT_PRESENT="validation.required.valueNotPresent";
const ONE_TIME_PASSWORD_INVALID = "one.time.password.invalid";

let isAccountCheckNetRequest = false;
let isSendEmailNetRequest = false;
let isPreResetNetRequest = false;

class ForgetPassword extends Component {
    state = {
        account: '',
        accountTipLevel: DEFAULT,
        accountVerifyTip: '',
        vercode: '',
        vercodeTipLevel: DEFAULT,
        vercodeVerifyTip: '',
        inputType: INPUT_DEFAULT,
        checked: false,
        requesting: false,
        resetInput: false,
        resetStep: RESET_STEP.INPUT_ACCOUNT,
        phoneNumber: '', // 已发送至电话号码
        vercodeTimeRemainToSave: 0,
        vercodeTimeRemainToGet: 0,
    }

    onAccountInputChange = ({ target: { value } }) => {
        this.setState({
            account: value,
        });
    }

    checkAccount = () => {
        if (isAccountCheckNetRequest) {
            return;
        }
        isAccountCheckNetRequest = true;
        this.refs.accountInput.verifyInput()
            .then((data) => {
                const { accountType, phoneNumber } = data;
                if (accountType === "PHONENUMBER") {
                    this.saveAccountForSmsHelpPage(); // 在获取语音前保存状态
                    this.setState({
                        resetStep: RESET_STEP.INPUT_CODE,
                        phoneNumber,
                    },
                    () => this.refs.vercodeInput.getVercode()) // 自动获取验证码)
                } else {
                    this.sendEmail();
                }
            })
            .catch(err => {
                console.log(err);
            }).finally(function () {
                isAccountCheckNetRequest = false;
            });
    }

    checkVercode = () => {
        if (isPreResetNetRequest) {
            return;
        }
        isPreResetNetRequest = true;
        this.refs.vercodeInput.verifyInput()
            .then((vercode) => {
                const speechSessionId = jsCookie.get('speechSessionId');

                let sessionId = speechSessionId ? speechSessionId : this.state.smsSessionId;
                let timeStamp = new Date().getTime();
    
                let params = {
                    sessionId: sessionId,
                    otp: vercode,
                    timeStamp: timeStamp,
                    recipient: this.state.account,
                };
                let sig = utils.getSignByParam(params);
    
                return get(PRE_RESET_PASSWORD,
                    {
                        params: {sig,...params}
                    });
            })
            .then((data) => {
                if(getValue(data, 'data')){
                    if(getValue(data, 'data.success')){
                        window.location.href = config.baseUrl + getValue(data, 'data.path');
                    }else {
                        const errors = getValue(data, 'data.errors')
                        if (errors.length) {
                            let error = errors[0];
                            switch (error.code) {
                                case ERROR_CODE_VALUE_INVALID:
                                case ERROR_CODE_VALUE_NOT_PRESENT:
                                case ONE_TIME_PASSWORD_INVALID:
                                    this.setState({
                                        vercodeVerifyTip: INVALID_VERIFICATION_CODE,
                                        vercodeTipLevel: ERROR,
                                        inputType: VERCODE,
                                    });
                                    break;
                                default:
                                    break;
                            }
                        }
                    }
                }
            }).catch(function (error) {
                console.log(error);
            }).finally(function () {
                isPreResetNetRequest = false;
            });
    }
    // hack处理模拟account的验证过程
    verifyAccountAndPassword = () => {
        return Promise.resolve({ account: this.state.account });
    }

    updateSmsSessionId= (smsSessionId) => {
        this.setState({
          smsSessionId,
        });
        jsCookie.set('smsSessionId', smsSessionId, { expires: 1 / 24 / 60 }); // 存一个60秒过期的 sessionId 防止用户在获取验证码后误点刷新页面导致60秒内无法注册
        if (smsSessionId) {
          jsCookie.remove('speechSessionId');
        }
    }

    saveAccountForSmsHelpPage() {
        // window.ga('send','event','account_signup','click_verification_code_help','');
        const { account } = this.state;
        jsCookie.set('registrationSpeechAccount', account, {
            expires: 1 / 24 / 60,
        });
    }

    onVercodeTimeChange = (time) => {
        this.setState({
            vercodeTimeRemainToSave: time,
        });
    }

    sendEmail = () => {
        const { saveBindLoginInfo: _saveBindLoginInfo } = this.props;
        const { account } = this.state;
        const _this = this;
        const timeStamp = new Date().getTime();

        if (isSendEmailNetRequest) {
            return;
        }
        isSendEmailNetRequest = true;

        get(SEND_EMAIL, {
            params: {
                identifier: account,
                timeStamp,
                sig: md5(account + timeStamp + config.reqKey),
                dbm: 'yxdbm',
            }
        }).then(function (data) {
            if(getValue(data, 'data')){
                if(getValue(data, 'data.success')){
                    // save state to the bind page
                    _saveBindLoginInfo({
                        account: account,
                    })
                    confirm({
                        content: EMAIL_SENT,
                        operates: () => _this.props.history.push(`/bind-existed`)
                    })
                }
            }
        }).catch(function (error) {
            console.log(error);
        }).finally(function () {
            isSendEmailNetRequest = false;
        });
    }

    redirectToTwoFactorHelper = () => {
        this.props.history.push('/two-factor-helper');
    }

    inputAccount = () => {
        const {
            account,
            accountTipLevel,
            accountVerifyTip,
            inputType,
            resetInput,
          } = this.state;
        const accountWasDisableTip = <DisableAccountTip account={account} />;
        return (
            <div className={styles.accountInputWrapper}>
                <div className={styles.descTitle}>{FORGET_DESC}</div>
                <LoginAccountInput
                    ref="accountInput"
                    tipLevel={accountTipLevel}
                    verifyTip={accountVerifyTip}
                    inputType={inputType}
                    account={account}
                    accountWasDisableHooks={this.accountWasDisable}
                    accountNotExistedHooks={this.accountNotExisted}
                    accountWasDisableTip={accountWasDisableTip}
                    onChange={this.onAccountInputChange}
                    resetInput={resetInput}
                    forgetCheck
                />
                <BasicButton text={RESET_PASSWORD}
                             buttonWrapperClass={styles.resetBtn}
                             onClick={this.checkAccount}
                             loading={isPreResetNetRequest}
                />
            </div>
        );
    }

    inputVercode = () => {
        const {
            inputType,
            vercodeVerifyTip,
            vercodeTipLevel,
            phoneNumber,
          } = this.state;
          
        return (
            <div className={styles.inputWrapper}>
              <div className={styles.sendMessage}>{copywritingHelper(AREADY_SEND)[0] + phoneNumber}</div>
              <VercodeInput
                ref="vercodeInput"
                verify={this.verifyAccountAndPassword}
                updateSmsSessionId={this.updateSmsSessionId}
                verifyTip={vercodeVerifyTip}
                tipLevel={vercodeTipLevel}
                inputType={inputType}
                updateCountState={() => {}}
                onVercodeTimeChange={this.onVercodeTimeChange}
              />
              <BasicButton text={NEXT_STEP}
                buttonWrapperClass={styles.resetBtn}
                onClick={this.checkVercode}
                loading={isPreResetNetRequest}
              />
              <div className={styles.canNotGetVercodeWrapper}>
                <CanNotGetVercode accountCreatePurpose={FORGET_PASSWORD} />
              </div>
            </div>
        )
    }

    render() {
        const { resetStep } = this.state;
        const { INPUT_ACCOUNT, INPUT_CODE } = RESET_STEP;
        return (
            <div className={styles.forgetPassword}>
                {resetStep === INPUT_ACCOUNT && this.inputAccount()}
                {resetStep === INPUT_CODE && this.inputVercode()}
                {/* {this.inputPassword()} */}
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
      bindRegistrationInfo: state.bindRegistrationInfo,
    };
};
const mapDispatchToProps = {
    saveBindLoginInfo: info => {
        return saveBindLoginInfo(info);
    },
};

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ForgetPassword);